import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
// #224D9E
export let scene, camera, renderer, controls, textureloader;
const innerWidth = window.innerWidth;
const innerHeight = window.innerHeight;

textureloader = new THREE.TextureLoader().setPath('./static/images');

export const init = (tag) => {
  scene = new THREE.Scene();
  camera = new THREE.PerspectiveCamera(
    45,
    innerWidth / innerHeight,
    0.01,
    9999
  );
  camera.position.set(-10, 15, -35);
  // camera.position.set(0, 0,20);
  scene.add(camera);

  // textureloader.load('/bg.jpg', (texture) => {
  //   // scene.background = texture;
  //   // scene.environment = texture;
  // });

  // const axesHelper = new THREE.AxesHelper(20);
  // scene.add(axesHelper);

  const light = new THREE.AmbientLight(0xffffff, 1);
  scene.add(light);

  renderer = new THREE.WebGLRenderer({ alpha: true });
  renderer.setSize(innerWidth, innerHeight);

  renderer.outputEncoding = THREE.sRGBEncoding;
  document.querySelector(tag).appendChild(renderer.domElement);

  controls = new OrbitControls(camera, renderer.domElement);
  //设置控制器阻尼
  controls.enableDamping = true;
  //设置自动旋转，界面自动旋转
  controls.autoRotate = true;
  controls.autoRotateSpeed = .2;
};

export function animate() {
  renderer.render(scene, camera);
  controls.update();
  window.requestAnimationFrame(animate);
}

window.addEventListener('resize', () => {
  controls.update();
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
  renderer.setSize(window.innerWidth, window.innerHeight);
  renderer.render(scene, camera);
  renderer.setPixelRatio(window.devicePixelRatio); // 设置渲染器的像素比
});
