<script>
import { onMounted, ref } from "@vue/runtime-core";
import gsap from "gsap";
// @ is an alias to /src
import { earthInit } from "../threejs/earth";
import { radarInit } from "../threejs/radar-chart";
import { ringInit } from "../threejs/ring-chart";
import {
  ringBreakInit,
  circleInit1,
  circleInit2,
  circleInit3,
} from "../threejs/ring-break-chart";

export default {
  name: "Home",
  components: {},
  setup() {
    const saveMoney = ref(0);
    onMounted(() => {
      earthInit();
      // radarInit();

      // ringInit();

      // ringBreakInit();

      // circleInit1();

      // circleInit2();

      // circleInit3();

      gsap.to(saveMoney, {
        value: 783849506,

        onComplete: () => {
          // console.log(saveMoney.value);
          const reg = /(\d)(?=(?:\d{3})+$)/g;
          saveMoney.value = saveMoney.value.toString().replace(reg, "$1,");
        },
        onUpdate: () => {
          saveMoney.value = Math.floor(saveMoney.value);
        },
      });
    });

    return { saveMoney };
  },
};
</script>
