import * as THREE from 'three';
import { Mesh, MeshStandardMaterial } from 'three';
import { scene, textureloader } from './init';
import allPointData from '../../echartsFn/chinaMap/pointLine';

import { getCoordinate, rangeRandom, lglt2xyzThreejs } from '../../utils';
import FlyLine from './flyLine';
import * as ChinaData from '../../map-data/chinamap.json';
import * as WordData from '../../map-data/worldmap.json';

export const earthRadius = 10; // 三地地图

export const earth = () => {
  const geom = new THREE.SphereBufferGeometry(earthRadius);
  const material = new MeshStandardMaterial({
    map: textureloader.load('/earth/earth_atmos_4096.jpg'),
    normalMap: textureloader.load('/earth/earth_normal_2048.jpg'),
  });
  const mesh = new THREE.Mesh(geom, material);
  scene.add(mesh);

  const pointBox = new THREE.Mesh(
    new THREE.BoxGeometry(1, 1, 1),
    new THREE.MeshBasicMaterial({ color: 0xff0000 })
  );
  const pointGroup = new THREE.Group();
  allPointData.citys.forEach((item) => {
    const box = pointBox.clone();
    const xyz = lglt2xyzThreejs(...item.value, earthRadius);
    box.position.set(xyz.x, xyz.y, xyz.z);
    pointGroup.add(box);
  });
  // scene.add(pointGroup);

  const LineGroup = new THREE.Group();
  allPointData.moveLines.forEach((item, index) => {
    if (index % 3 == 0) {
      const fromNamezb = lglt2xyzThreejs(...item.coords[0], earthRadius);
      const toNamezb = lglt2xyzThreejs(...item.coords[1], earthRadius);

      const flyline2 = new FlyLine(fromNamezb, toNamezb, {
        lineType: 1,
        timeDuration: 1,
        scale: 0.001,
        color: new THREE.Color('#FFD01A'),
      });
      const mesh = flyline2.init();
      LineGroup.add(mesh);
    }
  });
  scene.add(LineGroup);

  const wordmapdata = getWordData(); // 120个数据[0,119]
  const chinamapdata = getChinaData(); // 35个数据 [0,34]

  const lineArr = [];
  setInterval(() => {
    if (lineArr.length > 3) {
      const m = lineArr[0];
      m.clear();
      LineGroup.remove(m);
      lineArr.splice(0, 1);
    }
    const flyline2 = new FlyLine(
      lglt2xyzThreejs(...chinamapdata[rangeRandom(0, 34)], earthRadius),
      lglt2xyzThreejs(...wordmapdata[rangeRandom(0, 100)], earthRadius),
      {
        scale: 0.008,
        lineType: 2,
        color: new THREE.Color('#FFD01A'),
        bulge: earthRadius,
        segment: 0.2,
        animate: true,
      }
    );
    const mesh = flyline2.init();
    LineGroup.add(mesh);
    lineArr.push(mesh);
  }, 4000);
};

const getWordData = () => {
  const WordArr = [];
  WordData.default.features.forEach((item) => {
    if (
      item.geometry &&
      item.geometry.coordinates &&
      item.geometry.coordinates[0][0].length == 2
    ) {
      WordArr.push(item.geometry.coordinates[0][0]);
    }
  });
  return WordArr; //120个数据
};

const getChinaData = () => {
  const WordArr = [];

  ChinaData.default.features.forEach((item) => {
    if (item.properties && item.properties.center) {
      WordArr.push(item.properties.center);
    }
  });
  return WordArr; //34个数据
};
