import * as THREE from 'three';
import { earthRadius } from '../threejs/earth/earth';
import { numLat, numLng } from '../threejs/earth/helper-sphere';
import { scene } from '../threejs/earth/init';
import FlyLine from '../threejs/earth/flyLine';
// import { chinaMap } from '../threejs/earth';

export function rangeRandom(m, n) {
  return Math.floor(Math.random() * (m - n) + n);
}

export function getCoordinate(iLat, jLng) {
  // 经, 纬
  //  iLat = 0-100  jLng = 0-200
  // jLng  x轴方向  y 至 -y 180度
  // iLat z轴方向, x 至 x 360度
  const lat = (Math.random() * Math.PI) / 50.0 + (iLat / numLat) * Math.PI; // / 17;
  const lng = (Math.random() * Math.PI) / 50.0 + (jLng / numLng) * 2 * Math.PI; //  / 4;
  return {
    x: earthRadius * Math.sin(lat) * Math.cos(lng),
    y: earthRadius * Math.cos(lat),
    z: earthRadius * Math.sin(lat) * Math.sin(lng),
  };
}

export function getCoordinatePlane() {
  return {
    x: rangeRandom(-3, 3),
    y: rangeRandom(-3, 3),
    z: 0,
  };
}



/**经纬度转xyz
 *lng:经度
 *lat:维度
 *radius:地球半径
 */
function lglt2xyz(lng, lat, radius) {
  const phi = (180 + lng) * (Math.PI / 180);
  const theta = (90 - lat) * (Math.PI / 180);
  return {
    x: -radius * Math.sin(theta) * Math.cos(phi),
    y: radius * Math.cos(theta),
    z: 0, //radius * Math.sin(theta) * Math.sin(phi),
  };
}

/**Threejs自带的方法
 *lng:经度
 *lat:维度
 *radius:地球半径
 */

export function lglt2xyzThreejs(lng, lat, radius) {
  const theta = (90 + lng) * (Math.PI / 180);
  const phi = (90 - lat) * (Math.PI / 180);
  return new THREE.Vector3().setFromSpherical(
    new THREE.Spherical(radius, phi, theta)
  );
}
